@import url(https://fonts.googleapis.com/css2?family=Goblin+One&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #b92b27, #1565c0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.title{font-family:'Goblin One', cursive;text-transform:uppercase;font-size:2rem;background:linear-gradient(to right, #09009f, #00ff95 80%);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent}

input[type=search]:focus{outline:2px solid blue}

